.home-page .banner {
  text-align: center;
  padding: 80px 20px;
  background: url(/static/img/zhichou-banner.886b8af.jpg) center no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
}
.home-page .banner img {
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: auto;
  margin: 0 auto;
  position: absolute;
}
.home-page .banner h1 {
  color: #fff;
  font-size: 42px;
  margin: 0;
}
.home-page .banner h3 {
  color: #fff;
  font-size: 24px;
  margin-top: 28px;
}
.home-page .nav {
  padding-left: 0;
  list-style: none;
  max-width: 1065px;
  margin: 20px auto 0 auto;
}
.home-page .nav a {
  float: left;
  margin: 17px;
  width: 320px;
  color: #32ADC6;
  text-align: center;
  display: block;
  padding: 60px 0 40px 0;
  border: 1px solid #32ADC6;
  position: relative;
}
.home-page .nav a:hover {
  text-decoration: none;
}
.home-page .nav a:hover .watermark {
  color: #fff !important;
}
.home-page .nav a:hover .zui-logo {
  display: none;
}
.home-page .nav a:hover .zui-logo.mobile {
  display: block;
}
.home-page .nav a.zhichou:hover {
  color: #fff;
  background: #35A3BA;
}
.home-page .nav a.hr:hover {
  color: #fff;
  background: #6AC2B5;
}
.home-page .nav a.plus:hover {
  color: #fff;
  background: #000;
}
.home-page .nav a .watermark {
  top: 0;
  left: 0;
  opacity: .1;
  font-size: 70px;
  line-height: 70px;
  position: absolute;
}
.home-page .nav a .watermark.blue {
  color: #35A3BA;
}
.home-page .nav a .watermark.green {
  color: #00d6b2;
}
.home-page .nav a .zui-logo {
  display: block;
  margin: 0 auto;
}
.home-page .nav a .zui-logo.mobile {
  display: none;
}
.home-page .nav a h3 {
  font-size: 24px;
  margin-top: 15px;
}
.home-page .nav a p {
  font-size: 18px;
  margin-top: 14px;
}
.home-page .nav a.hr {
  color: #6AC2B5;
  border-bolor: #6AC2B5;
}
.home-page .nav a.plus {
  color: #35A3BA;
  border-color: #35A3BA;
}
.home-page .join-zhichou {
  color: #32ADC6;
  font-size: 16px;
  text-align: center;
  width: 100%;
  display: block;
  margin: 30px 0 165px 0;
}
@media (max-width: 420px) {
  .home-page .banner h1 {
    font-size: 24px;
  }
  .home-page .banner h1 .br {
    display: block;
  }
  .home-page .banner h3 {
    font-size: 12px;
  }
  .home-page .nav {
    margin: 35px auto 0 auto;
  }
  .home-page .nav a {
    float: none;
    width: 270px;
    margin: 25px auto;
    padding: 32px 20px;
  }
  .home-page .nav a.zhichou {
    color: #fff;
    background: #32ADC6;
  }
  .home-page .nav a.hr {
    color: #fff;
    background: #6AC2B5;
  }
  .home-page .nav a.plus {
    color: #fff;
    background: #000;
  }
  .home-page .nav a .watermark {
    color: #fff !important;
  }
  .home-page .nav a .zui-logo {
    width: 110px;
    display: none;
  }
  .home-page .nav a .zui-logo.mobile {
    display: block;
  }
  .home-page .nav a h3 {
    font-size: 16px;
    margin-top: 10px;
  }
  .home-page .nav a h4 {
    font-size: 12px;
  }
  .home-page .join-zhichou {
    margin: 0 0 30px 0;
  }
}
.zui-logo {
  width: 150px;
  height: 52px;
  cursor: pointer;
}
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}
html {
  -ms-touch-action: none;
}
html,
body {
  -webkit-text-size-adjust: none;
  margin: 0;
  height: 100%;
  font-size: 14px !important;
}
html ::-webkit-scrollbar,
body ::-webkit-scrollbar {
  width: 0;
}
ul,
ol {
  list-style: none;
}
a {
  color: #32ADC6;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
button {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
input,
button,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  border: none;
  outline: none;
}
input:active,
textarea:active,
select:active {
  border: none;
}
input,
textarea,
select {
  font-size: 14px;
}
img {
  border: none;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #999;
}
input:-moz-placeholder,
textarea:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #999;
}
input::-moz-placeholder,
textarea::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #999;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #999;
}
/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.zui-page {
  width: 100%;
  min-height: 100%;
  margin: 0 auto;
  font-family: "\5FAE\8F6F\96C5\9ED1", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.zui-page.min-1200 {
  min-width: 1200px;
}
.zui-page a:hover {
  color: #35A3BA;
  text-decoration: underline;
}
.zui-page .clear:after {
  content: '';
  display: table;
  clear: both;
}
